@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PTMono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto-Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto-Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "PTMono", "Arial", sans-serif;
  color: #f2f5f6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}

.no-js .footer__title::before {
  display: none;
}
.no-js .footer__list {
  max-height: 100%;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--button-call {
  background-color: rgba(255, 255, 255, 0);
  border: solid 1px #f2f5f6;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  width: 160px;
  text-transform: uppercase;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .btn {
    display: none;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 32px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn svg {
  fill: #ffffff;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: fixed;
  z-index: 5;
  width: 100%;
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
  }
}

.header__wrapper {
  display: flex;
  column-gap: 160px;
  row-gap: 10px;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 30px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    flex-wrap: nowrap;
    padding-top: 10px;
    padding-bottom: 20px;
    padding-right: 35px;
    column-gap: 125px;
  }
}

.header__list {
  display: flex;
  column-gap: 125px;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .header__list {
    column-gap: 20px;
    margin-right: 18px;
  }
}

@media (max-width: 1023px) {
  .header__item {
    width: 40px;
    height: 40px;
  }
}

.header__link {
  display: flex;
  column-gap: 15px;
  line-height: 18px;
  color: #f2f5f6;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__link.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .header__link {
    grid-column-gap: 30px;
    padding: 10px;
  }
}

@media (max-width: 1023px) {
  .header__link-text {
    display: none;
  }
}

.logo__link {
  display: flex;
  align-items: center;
  column-gap: 15px;
}

.logo__img {
  display: block;
}
@media (max-width: 767px) {
  .logo__img--header {
    display: none;
  }
}

.logo__mobile-img {
  display: none;
}
@media (max-width: 767px) {
  .logo__mobile-img {
    display: block;
  }
}

.footer {
  min-height: 280px;
  background-color: #041827;
  padding: 50px 0 55px;
  overflow: hidden;
}
@media (max-width: 767px) {
  .footer {
    padding-bottom: 35px;
    padding-top: 40px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: auto 30% 28%;
  justify-content: space-between;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    grid-template-columns: 48% 48%;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-columns: 100%;
  }
}

.footer__icon-list {
  display: flex;
  column-gap: 25px;
  padding: 0;
  margin: 40px 15px 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .footer__icon-list {
    margin: 0;
  }
}

.footer__list-wrapper {
  position: relative;
  width: 100%;
}
@media (max-width: 767px) {
  .footer__list-wrapper::before {
    position: absolute;
    top: 0;
    left: -25px;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.6980392157);
  }
  .footer__list-wrapper::after {
    position: absolute;
    bottom: 0;
    left: -25px;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.6980392157);
  }
}

@media (max-width: 767px) {
  .footer__list-wrapper:last-child::before {
    display: none;
  }
}

.footer__list {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  row-gap: 11px;
  column-gap: 30px;
  padding: 0;
  margin: 0;
  list-style: none;
  overflow: hidden;
  transition: 0.3s;
  max-height: auto;
}
.footer__list--site {
  height: 144px;
}
@media (max-width: 1023px) {
  .footer__list--site {
    height: 100%;
  }
}
@media (max-width: 1023px) {
  .footer__list {
    max-height: auto;
    flex-wrap: nowrap;
  }
}
@media (max-width: 767px) {
  .footer__list {
    max-height: 0;
  }
}

.footer__item {
  opacity: 0.7;
}
@media (max-width: 767px) {
  .footer__item {
    font-size: 14px;
    line-height: 19px;
  }
}

@media (max-width: 767px) {
  .footer__item:last-child {
    padding-bottom: 20px;
  }
}

.footer__list-link {
  color: #ffffff;
}
@media (hover: hover) {
  .footer__list-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__list-link.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 767px) {
  .footer__list-link {
    font-size: 14px;
    line-height: 19px;
  }
}

.footer__title {
  position: relative;
  margin: 0;
  padding: 10px 0 22px;
  font-size: 15px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: 400;
}
.footer__title.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .footer__title {
    padding: 0 0 20px;
  }
}
@media (max-width: 767px) {
  .footer__title {
    padding-top: 20px;
  }
  .footer__title::after, .footer__title::before {
    position: absolute;
    display: block;
    content: "";
    right: 0;
    top: 50%;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  .footer__title::before {
    rotate: 90deg;
  }
  .footer__title--active::before {
    display: none;
  }
}

.footer__logo {
  width: 100%;
}
@media (max-width: 1023px) {
  .footer__logo {
    display: flex;
    grid-column: 1/-1;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin-right: 0;
    margin-bottom: 25px;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 15px;
  }
}

@media (hover: hover) {
  .footer__icon-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__icon-link.focus-visible:focus {
  opacity: 0.8;
}

.intro {
  min-height: 880px;
  background-image: url("../img/main-background.jpg");
  background-color: #041827;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 120px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro {
    background-image: url("../img/main-background@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .intro {
    min-height: 640px;
    margin-bottom: 80px;
    background-image: url("../img/main-background-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .intro {
    background-image: url("../img/main-background-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 560px;
    margin-bottom: 50px;
    background-image: url("../img/main-background-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .intro {
    background-image: url("../img/main-background-mobile@2x.jpg");
  }
}

.intro__wrapper {
  max-width: 1024px;
  margin: 0 auto;
  padding: 300px 25px 0 25px;
}
@media (max-width: 1023px) {
  .intro__wrapper {
    padding: 180px 50px 0 50px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    padding: 150px 25px 0 25px;
  }
}

.intro__title {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  line-height: 94px;
  font-size: 80px;
  margin: 0;
  margin-bottom: 40px;
}
@media (max-width: 1023px) {
  .intro__title {
    font-size: 64px;
    line-height: 75px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .intro__title {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 8px;
  }
}

.intro__text {
  font-size: 22px;
