  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #ffffff;
  left: 2%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 3px;
